/*--------------------------------------------------------------
# Code Editor
--------------------------------------------------------------*/
.wecodeart-advanced-css__editor {
  border: 1px solid #e2e4e7;
  margin-bottom: 20px; }

.wecodeart-advanced-css__help {
  background: #f7f7f7;
  padding: 20px; }

/*--------------------------------------------------------------
# Backgrounds
--------------------------------------------------------------*/
.components-editorskit-background-color__indicator {
  position: absolute;
  background-color: currentColor;
  height: 1px;
  width: 20px;
  bottom: 9px;
  left: auto;
  right: auto;
  margin: 0 3px 0 7px; }

.has-inline-background {
  padding: 0.12em 4px;
  border-radius: 2px; }

/*--------------------------------------------------------------
# Text Colors
--------------------------------------------------------------*/
.components-wca-inline-color__indicator {
  position: absolute;
  background: #000;
  height: 3px;
  width: 20px;
  bottom: 6px;
  left: auto;
  right: auto;
  margin: 0 5px; }
  .components-wca-inline-color__indicator::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1); }

.components-wca-toolbar__control.components-icon-button.has-text svg {
  margin-right: 0; }

.components-wca__inline-color-popover .components-popover__content {
  padding: 10px 18px; }
  .components-wca__inline-color-popover .components-popover__content .components-color-palette {
    margin-top: 0.6rem; }
  .components-wca__inline-color-popover .components-popover__content .component-color-indicator {
    vertical-align: text-bottom; }
  .components-wca__inline-color-popover .components-popover__content .components-base-control__title {
    display: block;
    margin-bottom: 16px;
    font-weight: 600;
    color: #191e23; }

.block-editor-block-list__block .block-editor-block-contextual-toolbar .components-toolbar.wca-components-toolbar {
  border-right: 0;
  border-left: 0; }

/*--------------------------------------------------------------
# Hide Title
--------------------------------------------------------------*/
body.wca-title-hidden .editor-post-title__block .editor-post-title__input {
  opacity: 0.5; }

body.wca-title-hidden .wca-hidden-title-label {
  display: block; }

.wca-hide-title-label.components-base-control .components-base-control__help {
  margin-top: 5px;
  padding: 1px 10px;
  margin-left: 0;
  font-size: 12px;
  font-style: italic; }

/*--------------------------------------------------------------
# Code Editor
--------------------------------------------------------------*/
.edit-post-text-editor {
  max-width: 99% !important; }
  .edit-post-text-editor__body .editor-post-title__block {
    max-width: 100% !important; }
  .edit-post-text-editor__body .CodeMirror {
    /* stylelint-disable-line selector-class-pattern */
    height: auto; }
    .edit-post-text-editor__body .CodeMirror.CodeMirror-wrap {
      /* stylelint-disable-line selector-class-pattern */
      border: 1px solid #ddd; }
    .edit-post-text-editor__body .CodeMirror .CodeMirror-scroll {
      /* stylelint-disable-line selector-class-pattern */
      height: auto !important;
      overflow: visible !important; }

.wca-editor-loaded .edit-post-text-editor,
.wca-editor-loaded .edit-post-visual-editor {
  animation: slide-up 0.4s ease; }

@keyframes slide-up {
  0% {
    opacity: 0;
    transform: translateY(10px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

